<template>
  <div class="p2">
    <div v-if="CWBB">
      <div class="blB" style="display: block;">
        <p>财务报表申报表审核结果</p>
        <p>数据已上报，请稍后查询反馈信息。</p>
      </div>
    </div>
    <div v-else>
      <div class="blA">
        <!--增值税申报表审核结果-->
        <p style="height:150px; line-height:200px; font-size:24px; color:#F00;text-align: center;">
          数据审核无误，请点击“确认申报”进行申报！</p>
        <button @click="declare" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 确认申报</button>
      </div>
      <div class="blB">
        <p style="font-size: 24px; font-weight: bold; color: #999;height: 150px;line-height: 150px;">财务报表申报表审核结果</p>
        <p style=" font-size:24px; color:#F00;">数据已上报，请稍后查询反馈信息。</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        name: 37,
        CWBB: this.$store.state.isConfirmed.CWBB
      }
    },
    methods: {
      declare: function () {
        J('.blA').css('display', 'none')
        J('.blB').css('display', 'block')
        // sessionStorage.setItem("vatDeclare", 1);  //添加到sessionStorage
        this.$store.commit('saveConfirm', { name: 'CWBB', data: true })
      }
    }
  }
</script>

<style scoped>
  .p2 {
    text-align: center;
  }

  .blB {
    display: none;
  }
</style>



